<template>
    <div>
        <div class=title>周末去哪儿</div>
        <ul>
            <li class='item border-bottom' v-for="item of weekendList" :key="item.id">
                <div class="item-img-wrapper">
                    <img class='item-img' :src=item.imgUrl alt=''>
                </div>
                <div class='item-info'>
                    <p class='item-title'>{{item.title}}</p>
                    <p class='item-desc'>{{item.desc}}</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    weekendList: Array
  }
}
</script>

<style lang='stylus' scoped>
.title
    line-height .8rem
    background-color #eee
    padding-left .2rem

.item
    width 100%
    margin-top .1rem

    .item-img-wrapper
        overflow hidden
        height 0
        padding-bottom 37.04%

        .item-img
            width 100%

    .item-info
        padding .1rem
        margin-left .1rem
        min-width 0

        .item-title
            font-size .32rem
            font-weight bold
            ellipsis()

        .item-desc
            padding .2rem 0
            color #ccc
            ellipsis()
</style>



