<template>
<div class="header">
    <div class="header-left">
        <span class="iconfont back-icon">&#xe658;</span>
    </div>
    <div class="header-input">
        <span class="iconfont">&#xe720;</span>
        输入城市/景点/游玩主题
    </div>
    <router-link to="/city">
        <div class="header-right">
            {{this.city}}
            <span class="iconfont arrow-icon">&#xe65c;</span>
        </div>
    </router-link>
</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
    @import "~styles/varibles.styl"
    .header
        display flex
        height .86rem
        line-height .86rem
        background $bgcolor
        color #fff
        .header-left
            width: .64rem
            float left
            text-align center
            .back-icon
                font-size .6rem
        .header-input
            flex 1
            height: .64rem
            line-height .64rem
            padding-left .2rem
            margin-top .12rem
            margin-left .2rem
            background #fff
            border-radius .1rem
            color #ccc
        .header-right
            min-width: 1.04rem
            padding 0 .1rem
            float right
            text-align center
            color #fff
            .arrow-icon
                margin-left -.08rem
</style>



