<template>
    <swiper :options="swiperOption">
        <swiper-slide v-for="(page, index) of pages" :key="index">
            <div class='icons'>
                <div class='content' v-for="item of page" :key="item.id">
                    <div class='icon'>
                        <img  class='icon-img' :src=item.imgUrl alt=''>
                    </div>
                    <p class='icon-dsc'>{{item.desc}}</p>
                </div>
            </div>
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    iconList: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang='stylus' scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.icons
    overflow hidden
    height 0
    padding-bottom 50%
    .content
        position relative
        overflow hidden
        float left
        width 25%
        height 0
        padding-bottom 25%
        text-align center
        .icon
            position absolute
            left 0
            top 0
            right 0
            bottom .44rem
            box-sizing border-box
            padding .1rem
            .icon-img
                height 100%
        .icon-dsc
            position absolute
            left 0
            right 0
            bottom 0
            height .44rem
            line-height .44rem
            text-align center
            color $darkTextColor
            ellipsis()
</style>



